a {
    color: white;
}

.gray {
    color: gray;
}

.green {
    color: green;
}

.red {
    color: red;
}

.logo {
    height: 50px;
    max-width: auto;
    border-right: white 2px;
    border-color: grey;
}

.password-toggle-icon {
    position: relative;
    right: -300px;
    top: -32px;
    cursor: pointer;

}

.mode,
.checkbox {
    cursor: pointer;
}

.RESET {
    display: none !important;
}

@media screen and (max-width: 390px) {
    .password-toggle-icon {
        right: -290px;
    }
}

@media screen and (max-width: 380px) {
    .password-toggle-icon {
        right: -280px;
    }
}

@media screen and (max-width: 370px) {
    .password-toggle-icon {
        right: -265px;
    }
}

@media screen and (max-width: 280px) {
    .password-toggle-icon {
        right: -180px;
    }
}


